































































































































































































































































































































































import {Component, Vue, Watch} from 'vue-property-decorator';
import {Bounce, Elastic, gsap, Linear, Power1, TimelineLite} from "gsap";

@Component
export default class VueNewspaperComponent extends Vue {
  protected cloudOne: TimelineLite = new TimelineLite;
  protected cloudTwo: TimelineLite = new TimelineLite;
  protected cloudThree: TimelineLite = new TimelineLite;
  protected cloudFour: TimelineLite = new TimelineLite;
  protected cloudFive: TimelineLite = new TimelineLite;
  protected emailValue: string = '';
  protected showPlaceHolder: boolean = true;

  protected mounted(): void {
    this.initialize();
  }

  protected initialize(): void {
    this.cloudOne = gsap.timeline([
    '#cloud-one',
      36,
      {
        x: '-720px',
        ease: Linear.easeNone,
      },
    ]);
    this.cloudTwo = gsap.timeline([
      '#cloud-two',
      24,
      {
        x: '-720px',
        ease: Linear.easeNone,
      },
    ]);
    this.cloudThree = gsap.timeline([
    '#cloud-three',
      33,
      {
        x: '-720px',
        ease: Linear.easeNone,
      },
    ])
    this.cloudFour = gsap.timeline([
       '#cloud-four',
      33,
      {
        x: '-720px',
        ease: Linear.easeNone,
      },
    ]);
    this.cloudFive = gsap.timeline([
      '#cloud-five',
      24,
      {
        x: '-720px',
        ease: Linear.easeNone,
      },
    ]);

    gsap.set('#mail', {
      scale: 0,
      x: 20,
      transformOrigin: 'right',
    });
  }

  protected hoverBtn(): void {
    gsap.to('#btnSVG',0.3, {fill: '#fd1b66'});
  }

  protected disableHoverBtn(): void {
    gsap.to('#btnSVG', 0.3, {fill: '#ea5a87'});
  }

  protected submitAction(): void {
    let submit = gsap.timeline();
    submit.to(
        '#title, #disclaimer, #btn,#email, #placeholder',
        {
          opacity: 0,
          pointerEvents: 'none',
          ease: Power1.easeInOut,
        }
    );
    submit.to('inputSVG, #btnSVG', 0.5, {
      morphSVG: {shape: 'invisible-heart'},
      ease: Power1.easeInOut,
    });
    submit.to('#inputSVG, #btnSVG', 0.5, {
      y: -30,
      ease: Power1.easeInOut,
    });
    submit.to('#success', 0.5, {
      delay: -0.5,
      opacity: 1,
      ease: Power1.easeInOut,
    });

    let mailbox = gsap.timeline();
    mailbox.to({}, 1, {});

    mailbox.to('#mailbox-stick', 3, {
      rotate: -90,
      x: 15,
      transformOrigin: 'left',
      ease: Elastic.easeOut,
    });

    mailbox.to('#cover-closed', 1, {
      delay: -3,
      y: -1,
      rotation: -180,
      transformOrigin: 'bottom',
      ease: Bounce.easeOut,
    });

    mailbox.to('#cover-open-side', 0.15, {delay: -2, opacity: 0});

    mailbox.to('#heart', 0.15, {delay: -3, opacity: 0});

    mailbox.to('#mail', 0, {delay: -3, opacity: 1, display: 'block'});
    mailbox.to('#mail', 1, {
      delay: -3,
      scale: 1,
      x: 0,
      transformOrigin: 'right',
      ease: 'back.out',
    });

    let wrapper = gsap.timeline({delay: 3});
    wrapper.to('#svgWrapper', 1, {scale: 0.8, ease: Bounce.easeOut});

    wrapper.to('#svgWrapper', 1, {
      delay: -0.6,
      x: '-200%',
      rotate: 9,
      ease: Power1.easeIn,
    });

    wrapper.to(
        '#title, #disclaimer, #btn,#email, #placeholder',
        0,
        {
          opacity: 1,
          pointerEvents: 'all',
        }
    );
    wrapper.to('#inputSVG', 0, {morphSVG: {shape: 'inputSVG'}, y: 0});
    wrapper.to('#btnSVG', 0, {morphSVG: {shape: 'btnSVG'}, y: 0});
    wrapper.to('#success', 0, {opacity: 0});
    wrapper.to('#mailbox-stick', 0, {
      rotate: 0,
      x: 0,
      transformOrigin: 'left',
    });
    wrapper.to('#cover-closed', 0, {
      rotation: 0,
      y: 0,
      transformOrigin: 'bottom',
    });
    wrapper.to('#cover-open-side', 0, {opacity: 1});
    wrapper.to('#heart', 0, {opacity: 1});
    wrapper.to('#mail', 0, {
      opacity: 0,
      scale: 0,
      x: 0,
      transformOrigin: 'right',
      display: 'none',
    });
    wrapper.to('#svgWrapper', 0, {x: '200%'});
    wrapper.to('#svgWrapper', 1.5, {x: '0', rotate: -9, ease: 'back.out'});
    wrapper.to('#svgWrapper', 1, {
      delay: -0.5,
      rotate: 0,
      scale: 1,
      ease: Bounce.easeOut,
    });
  }

  @Watch('emailValue')
  protected onEmailValueChanged(): void {
    this.showPlaceHolder = !this.emailValue;
  }
}
